<template>
	<div>
		<div class="search">
			<input class="searchInput" type="text" placeholder="输入城市名或拼音" v-model="keyword">
		</div>
		<div class="search-content" ref="search" v-show="keyword">
				<ul>
					<li class="search-item border-bottom" 
						v-for="item of list" 
						:key="item.id"
						@click="handleCityClick(item.name)"
						>{{item.name}}</li>
					<li class="search-item border-bottom" v-show="hasNodata">没有找到匹配数据</li>	
				</ul>
		</div>
	</div>
</template>

<script>
	import Bscroll from 'better-scroll'

	export default{
		name:"CitySearch",
		props:{
			cities:Object
		},
		data(){
			return{
				keyword: '',
				list:[],
				timer:null
			}
		},
		computed:{
			hasNodata(){
				return !this.list.length
			}
		},
		methods:{
				handleCityClick(city){
				this.$store.commit('changeCity',city)
				this.$router.push('/')
			}
		},
		watch:{
			keyword(){
				if(this.timer){
					clearTimeout(this.timer)
				}
				if(!this.keyword){
					this.list = []
					return
				}
				this.timer = setTimeout(()=>{
					const result = []
					for(let i in this.cities){
						this.cities[i].forEach((value)=>{
							if(value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1){
								result.push(value)
							}
						})
					}
					this.list = result
				},100)
			}
		},
		mounted(){
			this.scroll = new Bscroll(this.$refs.search)
		}
	}
</script>


<style lang="stylus" scoped>
	@import '~style/varibles.styl'
		.search
			padding:.2rem
			background:$bgColor
			display:flex
			justify-content:center
			align-items:center
			.searchInput
				width:100%
				height:.62rem
				lien-height:.62rem
				text-align:center
				border-radius:.06rem
				border:1px solid #ccc
				box-sizing:border-box
				padding:0 .2rem
				color:#666
		.search-content	
			z-index:1
			position:absolute
			overflow:hidden
			top:2rem
			left:0
			right:0
			bottom:0
			background:#eee
			.search-item
				line-height:.62rem
				padding-left:.2rem
				background:#fff
				color:#666


</style>


